#sidebar {
  background-image: url("/images/common/cover.jpg");
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  overflow: auto;
  height: 100%; }
  #sidebar .sidebar-profile {
    color: #ebebeb;
    text-align: center;
    margin-bottom: 15px; }
    #sidebar .sidebar-profile a {
      display: inline-block; }
      #sidebar .sidebar-profile a img {
        width: 90px;
        height: 90px;
        border-radius: 90px;
        margin-bottom: 5px; }
    #sidebar .sidebar-profile h4 {
      margin: 20px auto; }
    #sidebar .sidebar-profile h5 {
      margin: 15px auto; }
  #sidebar .sidebar-buttons li a {
    color: #ebebeb;
    width: 100%;
    display: block; }
  #sidebar .sidebar-buttons li span {
    vertical-align: middle; }
  @media only screen and (max-width: 767px) {
    #sidebar {
      left: -250px;
      width: 250px; }
      #sidebar .sidebar-profile {
        height: 130px;
        padding-top: 40px;
        padding-bottom: 7.5px; }
        #sidebar .sidebar-profile h5 {
          display: none; }
        #sidebar .sidebar-profile a {
          width: 90px;
          height: 90px;
          border-radius: 45px; }
        #sidebar .sidebar-profile .sidebar-profile-picture {
          width: 90px;
          height: 90px;
          border-radius: 45px;
          margin-bottom: 5px; }
      #sidebar .sidebar-buttons {
        padding-top: 15px; }
        #sidebar .sidebar-buttons li a {
          display: block;
          box-sizing: border-box;
          padding: 10px 0 10px 50px; }
          #sidebar .sidebar-buttons li a i {
            width: 30px;
            height: 30px; }
          #sidebar .sidebar-buttons li a span {
            margin-left: 10px; }
        #sidebar .sidebar-buttons li:hover a {
          color: #ffffff; } }
  @media only screen and (min-width: 768px) and (max-width: 1023px) {
    #sidebar {
      width: 75px;
      left: 0 !important; }
      #sidebar .sidebar-profile {
        padding-top: 18px;
        margin-bottom: 15px; }
        #sidebar .sidebar-profile h4, #sidebar .sidebar-profile h5 {
          display: none; }
        #sidebar .sidebar-profile .sidebar-profile-picture {
          width: 30px;
          height: 30px;
          line-height: 30px;
          vertical-align: middle;
          border-radius: 30px; }
      #sidebar .sidebar-buttons li {
        height: 45px;
        line-height: 45px; }
        #sidebar .sidebar-buttons li:hover a {
          color: #ffffff; }
        #sidebar .sidebar-buttons li a {
          text-align: center; }
          #sidebar .sidebar-buttons li a span {
            display: none; } }
  @media only screen and (min-width: 1024px) and (max-width: 1279px) {
    #sidebar {
      width: 250px;
      left: 0 !important; }
      #sidebar .sidebar-profile {
        height: 130px;
        padding-top: 40px;
        padding-bottom: 7.5px; }
        #sidebar .sidebar-profile h5 {
          display: none; }
        #sidebar .sidebar-profile a {
          width: 90px;
          height: 90px;
          border-radius: 45px; }
        #sidebar .sidebar-profile .sidebar-profile-picture {
          width: 90px;
          height: 90px;
          border-radius: 45px;
          margin-bottom: 5px; }
      #sidebar .sidebar-buttons {
        padding-top: 15px; }
        #sidebar .sidebar-buttons li a {
          display: block;
          box-sizing: border-box;
          padding: 10px 0 10px 50px; }
          #sidebar .sidebar-buttons li a i {
            width: 30px;
            height: 30px; }
          #sidebar .sidebar-buttons li a span {
            margin-left: 10px; }
        #sidebar .sidebar-buttons li:hover a {
          color: #ffffff; } }
  @media only screen and (min-width: 1280px) {
    #sidebar {
      width: 500px;
      left: 0 !important;
      box-sizing: border-box;
      padding: 0 80px; }
      #sidebar .sidebar-profile {
        padding-top: 40px; }
      #sidebar .sidebar-buttons {
        margin-top: 50px; }
        #sidebar .sidebar-buttons li {
          height: 50px;
          width: 160px;
          float: left; }
          #sidebar .sidebar-buttons li:nth-child(even) i {
            margin-left: 30px; }
          #sidebar .sidebar-buttons li a:hover {
            color: #ffffff; }
          #sidebar .sidebar-buttons li a .sidebar-button-icon {
            text-align: center;
            width: 30px;
            height: 35px;
            line-height: 35px;
            padding-top: 0;
            vertical-align: middle;
            margin-right: 10px; } }
